import React from "react";
import { useLocation } from "react-router-dom";
import { API_URL, COINGECKO_TYPES } from "../config/constants";

export function timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}

export function authHeader() {
    let user = JSON.parse(localStorage.getItem('user'));

    if (user && user.token) {
        return { 'Authorization': 'Bearer ' + user.token };
    } else {
        return {};
    }
}

export const handleResponse = async (response, onError) => {
    const res = await response;
    const text = await res.text()

    const data = text && JSON.parse(text);
    if (!res.ok) {
        if (res.status === 401 && onError) {
            onError();
        }

        const error = (data && data.message) || res.statusText;
        throw new Error(error);
    }

    return data;
}

export const getImagePath = (file) => {
    return API_URL + '/uploads/' + file.path;
}

export const getAttachPath = (file) => {
    try {
        return API_URL + '/files/' + file.path + '/' + file.name;
    } catch(err) {
        console.log(err);
        return ''
    }
}

export const checkIsAdmin = (user) => {
    const admin_list = process.env.REACT_APP_ADMIN_USERS.split(",");
    return admin_list.indexOf(user) >= 0;
    
}

export const useQuery = () => {
    const { search } = useLocation();
  
    return React.useMemo(() => new URLSearchParams(search), [search]);
}

export const getRealestateQuery = (realestate, landlord) => {
    const query = realestate.name.toLowerCase().replace(/[^A-Z0-9]/ig, '-') + "-" 
                + landlord.firstname.toLowerCase().replace(/[^A-Z0-9]/ig, '-')  + "-" 
                + landlord.lastname.toLowerCase().replace(/[^A-Z0-9]/ig, '-');
    return query;
}

export const short_string = (text) => {
    return text.length > 50?(text.substr(0,50)+'...'):text;
}

export const listToObject = (obj) => {
    var ret = {};
    for (var i = 0; i < obj.length; i++) {
        var key = obj[i].productId;
        var val = obj[i].amount;
        ret[key] = val;
    }
    return ret;
}

export const objectToList = (obj) => {
    var ret = [];
    var keys = Object.keys(obj);
    for (var i = 0; i < keys.length; i++) {
        ret.push({productId: keys[i], amount:obj[keys[i]]});
    }
    return ret;
}

export const getCoinAmount = async (cointype, cost, vat, coindata, discount) => {    
    // {"bitcoin": {"usd": 28379}}
    const realcost = discount ? floatAdd(cost, floatMul(cost, -vat)) : cost;
    const coinprice = coindata[COINGECKO_TYPES[cointype]]['gbp'];
    const coinvalue = coinprice ? realcost / coinprice : realcost;

    return coinvalue;
}

export const getTotalAmount = (lists) => {
    let amount = 0;
    lists.forEach(list => {
        amount += list.amount;
    })
    return amount;
}

var _cf = (function() {
    function _shift(x) {
      var parts = x.toString().split('.');
      return (parts.length < 2) ? 1 : Math.pow(10, parts[1].length);
    }
    return function() { 
      return Array.prototype.reduce.call(arguments, function (prev, next) { return prev === undefined || next === undefined ? undefined : Math.max(prev, _shift (next)); }, -Infinity);
    };
})();

export function floatAdd () {
    var f = _cf.apply(null, arguments); if(f === undefined) return undefined;
    function cb(x, y, i, o) { return x + f * y; }
    return Array.prototype.reduce.call(arguments, cb, 0) / f;
};

export function floatMul () {
    var f = _cf.apply(null, arguments);
    function cb(x, y, i, o) { return (x*f) * (y*f) / (f * f); }
    return Array.prototype.reduce.call(arguments, cb, 1);
};

