import { createSlice } from '@reduxjs/toolkit';
import { productsService } from '../services/products.service';
import { openSnackBar } from './snackBarReducer';

//reducers
export const productsSlice = createSlice({
    name: 'products',
    initialState: {
        updatingProduct: false,
        deletingProduct: false,
        gettingProductData: true,
        products: [],
        classProducts: [],
        product: null
    },
    reducers: {
        setProducts: (state, action) => {
            state.products = action.payload
        },
        setClassProducts: (state, action) => {
            state.classProducts = action.payload
        },
        setUpdatingProduct: (state, action) => {
            state.updatingProduct = action.payload === undefined ? false : action.payload;
        },
        setGettingProductData: (state, action) => {
            state.gettingProductData = action.payload === undefined ? false : action.payload;
        },
        setDeletingProduct: (state, action) => {
            state.deletingProduct = action.payload === undefined ? false : action.payload;
        }
    }
});

//actions
export const { setProducts, setClassProducts, setUpdatingProduct, setGettingProductData, setDeletingProduct } = productsSlice.actions;

export const getProducts = () => async (dispatch) => {
    try {
        const productData = await productsService.getProducts();
        dispatch(setProducts(productData.data.data));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getProductsByCategory = (id) => async (dispatch) => {
    try {
        const productData = await productsService.getProductsByCategory(id);
        dispatch(setClassProducts(productData.data.products));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getProductsByKeyword = (keyword) => async (dispatch) => {
    try {
        const productData = await productsService.getProductsByKeyword(keyword);
        dispatch(setClassProducts(productData.data.products));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const updateProductData = (productId, product) => async (dispatch) => {
    dispatch(setUpdatingProduct(true));
    try {
        let respData = null;
        if (productId === 0) {
            respData = await productsService.registerProduct(product);
            dispatch(getProducts());
            dispatch(setUpdatingProduct(false));
            if ( respData.status === 'success' ) {
                dispatch(openSnackBar({ message: `Successfully registered product data!`, status: 'success' }));
            }
        } else {
            respData = await productsService.updateProduct(productId, product);
            dispatch(getProducts());
            dispatch(setUpdatingProduct(false));
            if ( respData.status === 'success' ) {
                dispatch(openSnackBar({ message: `Successfully updated product data!`, status: 'success' }));
            }
        }
    } catch (error) {
        dispatch(setUpdatingProduct(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const deleteProduct = (productId) => async (dispatch) => {
    dispatch(setDeletingProduct(true));

    try {
        await productsService.deleteProduct(productId);
        dispatch(setDeletingProduct(false));
        dispatch(getProducts());
        dispatch(openSnackBar({ message: `Successfully deleted product!`, status: 'success' }));
    } catch (error) {
        dispatch(setDeletingProduct(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

//selectors
export const selectProducts = (state) => state.productState.products;
export const selectClassProducts = (state) => state.productState.classProducts;
export const selectUpdatingProduct = (state) => state.productState.updatingProduct;
export const selectGettingProductData = (state) => state.productState.gettingProductData;

export default productsSlice.reducer;