import { authHeader, handleResponse } from "../utils/utils";
import { API_BASE } from "../config/constants";

const getCategoryById = async (id) => {
    const requestOptions = {
        method: 'GET',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/categories/${id}`, requestOptions)
    return await handleResponse(response);
}

const getCategories = async (id) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/categories`, requestOptions)
    return await handleResponse(response);
}

const registerCategory = async (category) => {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(category)
    };

    const response = fetch(`${API_BASE}/categories/0`, requestOptions)
    return await handleResponse(response);
}

const updateCategory = async (categoryId, data) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
    };

    const response = fetch(`${API_BASE}/categories/${categoryId}`, requestOptions)
    return await handleResponse(response);
}

const deleteCategory = async (categoryId) => {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    const response = fetch(`${API_BASE}/categories/${categoryId}`, requestOptions)
    return await handleResponse(response);
}


export const pcategoryService = {
    getCategoryById,
    getCategories,
    registerCategory,
    updateCategory,
    deleteCategory
};
