import { authHeader, handleResponse } from "../utils/utils";
import { API_BASE } from "../config/constants";

const getOrderById = async (id) => {
    const requestOptions = {
        method: 'GET',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/orders/${id}`, requestOptions)
    return await handleResponse(response);
}

const getOrders = async () => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/orders`, requestOptions)
    return await handleResponse(response);
}

const getOrdersByName = async (name) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/orders/name/${name}`, requestOptions)
    return await handleResponse(response);
}

const getOrdersBySale = async () => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/orders/sale`, requestOptions)
    return await handleResponse(response);
}

const registerOrder = async (order) => {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(order)
    };

    const response = fetch(`${API_BASE}/orders/0`, requestOptions)
    return await handleResponse(response);
}

const updateOrder = async (orderId, data) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader() },
        body: data
    };

    const response = fetch(`${API_BASE}/orders/${orderId}`, requestOptions)
    return await handleResponse(response);
}

const deleteOrder = async (orderId) => {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    const response = fetch(`${API_BASE}/orders/${orderId}`, requestOptions)
    return await handleResponse(response);
}

const getStatisticsOrders = async (fromDate, toDate) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify({fromDate, toDate})
    };

    const response = fetch(`${API_BASE}/orders/statistics`, requestOptions)
    return await handleResponse(response);
}

const refundOrder = async (orderId) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader() }
    };

    const response = fetch(`${API_BASE}/orders/refund/${orderId}`, requestOptions)
    return await handleResponse(response);
}

const paymentedOrder = async (orderId) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader() }
    };

    const response = fetch(`${API_BASE}/orders/paymented/${orderId}`, requestOptions)
    return await handleResponse(response);
}

const paystatusOrder = async (orderId) => {
    const requestOptions = {
        method: 'GET',
        headers: { ...authHeader() }
    };

    const response = fetch(`${API_BASE}/orders/paystatus/${orderId}`, requestOptions)
    return await handleResponse(response);
}

export const ordersService = {
    getOrderById,
    getOrders,
    getOrdersByName,
    getOrdersBySale,
    registerOrder,
    updateOrder,
    deleteOrder,
    getStatisticsOrders,
    refundOrder,
    paymentedOrder,
    paystatusOrder
};
