import { createSlice } from '@reduxjs/toolkit';
import { pcategoryService } from '../services/pcategory.service';
import { openSnackBar } from './snackBarReducer';
import { getProducts } from './productReducer';

//reducers
export const pcategorySlice = createSlice({
    name: 'pcategory',
    initialState: {
        updatingCategory: false,
        deletingCategory: false,
        gettingCategoryData: false,
        categories: []
    },
    reducers: {
        setCategories: (state, action) => {
            state.categories = action.payload
        },
        setUpdatingCategory: (state, action) => {
            state.updatingCategory = action.payload === undefined ? false : action.payload;
        },
        setGettingCategoryData: (state, action) => {
            state.gettingCategoryData = action.payload === undefined ? false : action.payload;
        },
        setDeletingCategory: (state, action) => {
            state.deletingCategory = action.payload === undefined ? false : action.payload;
        }
    }
});

//actions
export const { setCategories, setUpdatingCategory, setGettingCategoryData, setDeletingCategory } = pcategorySlice.actions;

export const getCategories = () => async (dispatch) => {
    try {
        dispatch(setGettingCategoryData(true));
        const categoryData = await pcategoryService.getCategories();
        dispatch(setCategories(categoryData.data.data));
        dispatch(setGettingCategoryData(false));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const updateCategoryData = (categoryId, category) => async (dispatch) => {
    dispatch(setUpdatingCategory(true));
    try {
        let respData = null;
        if (categoryId === 0) {
            respData = await pcategoryService.registerCategory(category);
            dispatch(getCategories());
            dispatch(setUpdatingCategory(false));
            if ( respData.status === 'success' ) {
                dispatch(openSnackBar({ message: `Successfully registered category data!`, status: 'success' }));
            }
        } else {
            respData = await pcategoryService.updateCategory(categoryId, category);
            dispatch(getCategories());
            dispatch(setUpdatingCategory(false));
            if ( respData.status === 'success' ) {
                dispatch(openSnackBar({ message: `Successfully updated category data!`, status: 'success' }));
            }
        }
    } catch (error) {
        dispatch(setUpdatingCategory(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const deleteCategory = (categoryId) => async (dispatch) => {
    dispatch(setDeletingCategory(true));

    try {
        await pcategoryService.deleteCategory(categoryId);
        dispatch(setDeletingCategory(false));
        dispatch(getCategories());
        dispatch(getProducts());
        dispatch(openSnackBar({ message: `Successfully deleted category!`, status: 'success' }));
    } catch (error) {
        dispatch(setDeletingCategory(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

//selectors
export const selectCategories = (state) => state.pcategoryState.categories;
export const selectUpdatingCategory = (state) => state.pcategoryState.updatingCategory;
export const selectGettingCategoryData = (state) => state.pcategoryState.gettingCategoryData;

export default pcategorySlice.reducer;