import { createSlice } from '@reduxjs/toolkit';

//reducers
export const settingSlice = createSlice({
    name: 'setting',
    initialState: {
        coin_allow_btc: true,
        coin_allow_eth: true,
        coin_allow_bnb: true,
        coin_allow_xrp: true,
        coin_allow_usdt: true,        
        coin_allow_cpos: true,
        discount: true,
    },
    reducers: {
        setCoinAllowBTC: (state, action) => {
            state.coin_allow_btc = action.payload
        },
        setCoinAllowETH: (state, action) => {
            state.coin_allow_eth = action.payload
        },
        setCoinAllowBNB: (state, action) => {
            state.coin_allow_bnb = action.payload
        },
        setCoinAllowXRP: (state, action) => {
            state.coin_allow_xrp = action.payload
        },
        setCoinAllowUSDT: (state, action) => {
            state.coin_allow_usdt = action.payload
        },
        setCoinAllowCPOS: (state, action) => {
            state.coin_allow_cpos = action.payload
        },
        syncCoinAllow: (state, action) => {
            state.coin_allow_btc = action.payload.coin_btc
            state.coin_allow_eth = action.payload.coin_eth
            state.coin_allow_bnb = action.payload.coin_bnb
            state.coin_allow_xrp = action.payload.coin_xrp
            state.coin_allow_usdt = action.payload.coin_usdt
            state.coin_allow_cpos = action.payload.coin_cpos
        },
        setAllowDiscount: (state, action) => {
            state.discount = action.payload
        }
    }
});

//actions
export const { setCoinAllowBTC, setCoinAllowETH, setCoinAllowBNB, setCoinAllowXRP, setCoinAllowUSDT, setCoinAllowCPOS, syncCoinAllow, setAllowDiscount } = settingSlice.actions;

//selectors
export const selectCoinAllowBTC = (state) => state.settingState.coin_allow_btc;
export const selectCoinAllowETH = (state) => state.settingState.coin_allow_eth;
export const selectCoinAllowBNB = (state) => state.settingState.coin_allow_bnb;
export const selectCoinAllowXRP = (state) => state.settingState.coin_allow_xrp;
export const selectCoinAllowUSDT = (state) => state.settingState.coin_allow_usdt;
export const selectCoinAllowCPOS = (state) => state.settingState.coin_allow_cpos;
export const selectAllowDiscount = (state) => state.settingState.discount;

export default settingSlice.reducer;