import { createSlice } from '@reduxjs/toolkit';
import { ordersService } from '../services/orders.service';
import { openSnackBar } from './snackBarReducer';
import { listToObject, objectToList } from '../utils/utils';

//reducers
export const ordersSlice = createSlice({
    name: 'orders',
    initialState: {
        updatingOrder: false,
        deletingOrder: false,
        gettingOrderData: true,
        orders: [],
        curOrders: [],
        curCount: 0,
        nameOrders: [],
        saleOrders: [],
        order: null,
        vat: 0.2,
        coinType: 0,
        customer: null,
        statisticsOrders: [],
        currentGroup: 0,
        waitingPaymentID: 0
    },
    reducers: {
        setOrders: (state, action) => {
            state.orders = action.payload
        },
        setNameOrders: (state, action) => {
            state.nameOrders = action.payload
        },
        setSaleOrders: (state, action) => {
            state.saleOrders = action.payload
        },
        setCurOrders: (state, action) => {
            state.curOrders = action.payload
        },
        setCurCount: (state, action) => {
            state.curCount = action.payload
        },
        setCustomer: (state, action) => {
            state.customer = action.payload
        },
        setCurrentGroup: (state, action) => {
            state.currentGroup = action.payload
        },
        addCurOrders: (state, action) => {
            const p = action.payload;
            const objOrders = listToObject(state.curOrders);

            state.curCount += p.amount;
            objOrders[p.productId] = objOrders[p.productId] >= 0 ? objOrders[p.productId] + p.amount : 1;            

            if (objOrders[p.productId] === 0) {
                delete objOrders[p.productId];
            }            
            const listOrders = objectToList(objOrders);

            state.curOrders = listOrders;
        },
        setVAT: (state, action) => {
            state.vat = action.payload;
        },
        resetVAT: (state, action) => {
            state.vat = 0.2;
        },
        setCoinType: (state, action) => {
            state.coinType = action.payload;
        },
        resetCoinType: (state, action) => {
            state.coinType = 0;
        },
        cleanCurOrders: (state) => {
            state.curOrders = [];
            state.curCount = 0;
        },
        setClassOrders: (state, action) => {
            state.classOrders = action.payload
        },
        setUpdatingOrder: (state, action) => {
            state.updatingOrder = action.payload === undefined ? false : action.payload;
        },
        setGettingOrderData: (state, action) => {
            state.gettingOrderData = action.payload === undefined ? false : action.payload;
        },
        setDeletingOrder: (state, action) => {
            state.deletingOrder = action.payload === undefined ? false : action.payload;
        },
        setStatisticsOrders: (state, action) => {
            state.statisticsOrders = action.payload
        },
        setWaitingPaymentID: (state, action) => {
            state.waitingPaymentID = action.payload
        }
    }
});

//actions
export const { setOrders, setNameOrders, setSaleOrders, setCurOrders, setCurCount, setCustomer, addCurOrders, cleanCurOrders, setClassOrders, setUpdatingOrder, setGettingOrderData, setDeletingOrder, setVAT, resetVAT, setCoinType, resetCoinType, setStatisticsOrders, setCurrentGroup, setWaitingPaymentID } = ordersSlice.actions;
export const getOrders = () => async (dispatch) => {
    try {
        const orderData = await ordersService.getOrders();
        dispatch(setOrders(orderData.data.orders));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getOrdersByName = (name) => async (dispatch) => {
    try {
        const orderData = await ordersService.getOrdersByName(name);
        dispatch(setNameOrders(orderData.data.orders));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getOrdersBySale = () => async (dispatch) => {
    try {
        const orderData = await ordersService.getOrdersBySale();
        dispatch(setSaleOrders(orderData.data.orders));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const updateOrderData = (orderId, order) => async (dispatch) => {
    dispatch(setUpdatingOrder(true));
    try {
        let respData = null;
        if (orderId === 0) {
            respData = await ordersService.registerOrder(order);
            dispatch(getOrders());
            dispatch(setUpdatingOrder(false));
            dispatch(setWaitingPaymentID(respData.order._id));
            if ( respData.status === 'success' && order.name !== "cpos_ordered" ) {
                dispatch(openSnackBar({ message: `Successfully registered order data!`, status: 'success' }));
            }
        } else {
            respData = await ordersService.updateOrder(orderId, order);
        
            dispatch(setUpdatingOrder(false));
            if ( respData.status === 'success' ) {
                dispatch(openSnackBar({ message: `Successfully updated order data!`, status: 'success' }));
            }
        }
    } catch (error) {
        dispatch(setUpdatingOrder(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const deleteOrder = (orderId, name='') => async (dispatch) => {
    dispatch(setDeletingOrder(true));

    try {
        await ordersService.deleteOrder(orderId);
        dispatch(setDeletingOrder(false));
        if (name.length > 0) dispatch(getOrdersByName(name))
        dispatch(getOrdersBySale());
        dispatch(openSnackBar({ message: `Successfully deleted order account!`, status: 'success' }));
    } catch (error) {
        dispatch(setDeletingOrder(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getStatisticsOrders = (fromDate, toDate) => async (dispatch) => {
    try {
        const result = await ordersService.getStatisticsOrders(fromDate, toDate);
        dispatch(setStatisticsOrders(result.data));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const refundOrder = (orderId) => async (dispatch) => {
    try {
        const result = await ordersService.refundOrder(orderId);
        if (result.status === 'success') {
            dispatch(getOrdersBySale());
            dispatch(openSnackBar({ message: `Successfully refunded order!`, status: 'success' }));
        }
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
}

export const paymentedOrder = (orderId) => async (dispatch) => {
    try {
        const result = await ordersService.paymentedOrder(orderId);
        if (result.status === 'success') {
            dispatch(getOrdersBySale());
            dispatch(openSnackBar({ message: `Successfully paymented order!`, status: 'success' }));
        }
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
}

//selectors
export const selectOrders = (state) => state.orderState.orders;
export const selectCustomer = (state) => state.orderState.customer;
export const selectNameOrders = (state) => state.orderState.nameOrders;
export const selectSaleOrders = (state) => state.orderState.saleOrders;
export const selectCurOrders = (state) => state.orderState.curOrders;
export const selectCurCount = (state) => state.orderState.curCount;
export const selectUpdatingOrder = (state) => state.orderState.updatingOrder;
export const selectGettingOrderData = (state) => state.orderState.gettingOrderData;
export const selectVAT = (state) => state.orderState.vat;
export const selectCoinType = (state) => state.orderState.coinType;
export const selectStatisticsOrders = (state) => state.orderState.statisticsOrders;
export const selectCurrentGroup = (state) => state.orderState.currentGroup;
export const selectWaitingPaymentID = (state) => state.orderState.waitingPaymentID;

export default ordersSlice.reducer;