import { authHeader, handleResponse } from "../utils/utils";
import { API_BASE } from "../config/constants";

const getProductById = async (id) => {
    const requestOptions = {
        method: 'GET',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/products/${id}`, requestOptions)
    return await handleResponse(response);
}

const getProducts = async (id) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/products`, requestOptions)
    return await handleResponse(response);
}

const getProductsByCategory = async (categoryId) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/products/category/${categoryId}`, requestOptions)
    return await handleResponse(response);
}

const getProductsByKeyword = async (keyword) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), }
    };

    const response = fetch(`${API_BASE}/products/keyword/${keyword}`, requestOptions)
    return await handleResponse(response);
}

const registerProduct = async (product) => {
    const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(product)
    };

    const response = fetch(`${API_BASE}/products/0`, requestOptions)
    return await handleResponse(response);
}

const updateProduct = async (productId, data) => {
    const requestOptions = {
        method: 'POST',
        headers: { ...authHeader(), 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
    };

    const response = fetch(`${API_BASE}/products/${productId}`, requestOptions)
    return await handleResponse(response);
}

const deleteProduct = async (productId) => {
    const requestOptions = {
        method: 'DELETE',
        headers: authHeader()
    };

    const response = fetch(`${API_BASE}/products/${productId}`, requestOptions)
    return await handleResponse(response);
}


export const productsService = {
    getProductById,
    getProducts,
    getProductsByCategory,
    getProductsByKeyword,
    registerProduct,
    updateProduct,
    deleteProduct
};
