import { createSlice } from '@reduxjs/toolkit';
import { userService } from '../services/users.service';
import { openSnackBar } from './snackBarReducer';
import { logout } from './authReducer';

//reducers
export const userSlice = createSlice({
    name: 'user',
    initialState: {
        updatingUser: false,
        deletingUser: false,
        gettingUserData: true,
        user: {},
        allUsers: [],
        allUserproxys: [],
        allCustomers: [],
        get_user: {},
        customers: [],
        totalUsers: []
    },
    reducers: {
        setUser: (state, action) => {
            state.user = action.payload
        },
        setAllUsers: (state, action) => {
            state.allUsers = action.payload
        },
        setAllUserproxys: (state, action) => {
            state.allUserproxys = action.payload
        },
        setAllCustomers: (state, action) => {
            state.allCustomers = action.payload
        },
        setCustomers: (state, action) => {
            state.customers = action.payload
        },
        setUpdatingUser: (state, action) => {
            state.updatingUser = action.payload === undefined ? false : action.payload;
        },
        setGettingUserData: (state, action) => {
            state.gettingUserData = action.payload === undefined ? false : action.payload;
        },
        setDeletingUser: (state, action) => {
            state.deletingUser = action.payload === undefined ? false : action.payload;
        },
        setGetUser: (state, action) => {
            state.get_user = action.payload
        },
        setRealestates: (state, action) => {
            state.realestates = action.payload === undefined ? [] : action.payload;
        },
        setSubmittedRealestates: (state, action) => {
            state.submitted = action.payload;
        },
        setTotalUsers: (state, action) => {
            state.totalUsers = action.payload
        }
    }
});

//actions
export const { setUser, setAllUsers, setAllUserproxys, setAllCustomers, setCustomers, setUpdatingUser, setGettingUserData, setDeletingUser, setGetUser, setRealestates, setSubmittedArtworks, setTotalUsers } = userSlice.actions;

export const getUserData = (userId) => async (dispatch) => {
    dispatch(setGettingUserData(true));
    try {
        const userData = await userService.getUserById(userId);
        dispatch(setUser(userData.data));
        dispatch(setGettingUserData(false));
    } catch (error) {
        dispatch(logout())
        dispatch(setUser({}))
        dispatch(setGettingUserData(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getAllUserData = (filterKey = '') => async (dispatch) => {    
    try {
        const userData = await userService.getAllUserData('all', filterKey);
        dispatch(setAllUsers(userData.users));
        dispatch(setAllCustomers(userData.customers));
    } catch (error) {
        dispatch(setAllUsers([]))
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getAllUserproxyData = (email, role, filterKey = '') => async (dispatch) => {    
    try {
        const userData = await userService.getAllUserproxyData(email, role, 'all', filterKey);
        dispatch(setAllUserproxys(userData.userproxys));
    } catch (error) {
        dispatch(setAllUserproxys([]))
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getAllCustomerData = (filterKey = '') => async (dispatch) => {    
    try {
        const userData = await userService.getAllUserData('customer', filterKey);
        dispatch(setAllCustomers(userData.customers));
    } catch (error) {
        dispatch(setAllCustomers([]))
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getCustomerByKeyword = (filterKey = '') => async (dispatch) => {    
    try {
        const userData = await userService.getAllUserData('customer', filterKey);
        dispatch(setCustomers(userData.customers));
    } catch (error) {
        dispatch(setAllUsers([]))
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const getUserById = (id) => async (dispatch) => {
    try {
        const userData = await userService.getUserById(id);
        dispatch(setGetUser(userData.data.user));
    } catch (error) {
        dispatch(setGetUser({}))
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const updateUserData = (userId, user) => async (dispatch) => {
    dispatch(setUpdatingUser(true));
    try {
        const respData = await userService.updateUser(userId, user);
        dispatch(setUpdatingUser(false));
        if ( respData.status === 'success' ) {
            dispatch(getAllUserData());
            dispatch(openSnackBar({ message: `Successfully updated ${respData.user.role} data!`, status: 'success' }));
        }
    } catch (error) {
        dispatch(setUpdatingUser(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const updateUserproxyData = (userproxyId, userproxy) => async (dispatch) => {
    dispatch(setUpdatingUser(true));
    try {
        const respData = await userService.updateUserproxy(userproxyId, userproxy);
        dispatch(setUpdatingUser(false));
        if ( respData.status === 'success' ) {
            dispatch(getAllUserproxyData(userproxy.email, userproxy.role));
            dispatch(openSnackBar({ message: `Successfully updated userproxy data!`, status: 'success' }));
        }
    } catch (error) {
        dispatch(setUpdatingUser(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const updateUserDataBy = (userId, user) => async (dispatch) => {
    dispatch(setUpdatingUser(true));
    try {
        const respData = await userService.updateUser(userId, user);
        dispatch(setUpdatingUser(false));
        if ( respData.status === 'success' ) {
            dispatch(setGetUser(respData.user))
            dispatch(openSnackBar({ message: `Successfully updated user data!`, status: 'success' }));
        }
    } catch (error) {
        dispatch(setUpdatingUser(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const deleteUser = (userId, kind) => async (dispatch) => {
    dispatch(setDeletingUser(true));

    try {
        await userService.deleteUser(userId);
        dispatch(setDeletingUser(false));
        dispatch(getAllUserData());
        dispatch(openSnackBar({ message: `Successfully deleted ${kind} account!`, status: 'success' }));
    } catch (error) {
        dispatch(setDeletingUser(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const deleteUserproxy = (email, role, userproxyId) => async (dispatch) => {
    dispatch(setDeletingUser(true));

    try {
        await userService.deleteUserproxy(userproxyId);
        dispatch(setDeletingUser(false));
        dispatch(getAllUserproxyData(email, role));
        dispatch(openSnackBar({ message: `Successfully deleted user!`, status: 'success' }));
    } catch (error) {
        dispatch(setDeletingUser(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

