export const updateTooltip = (type) => async (dispatch) => {
    dispatch(setUpdatingUser(true));
    try {
        const respData = await userService.updateTooltip(type);
        dispatch(setUpdatingUser(false));
        if ( respData.status === 'success' ) {
            dispatch(setUser(respData.user))
        }
    } catch (error) {
        dispatch(setUpdatingUser(false));
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const getTotalUserData = (filterKey = '') => async (dispatch) => {    
    try {
        const userData = await userService.getTotalUserData();
        dispatch(setTotalUsers(userData.data));
    } catch (error) {
        dispatch(setTotalUsers([]))
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

//selectors
export const selectUser = (state) => state.userState.user;
export const selectAllUsers = (state) => state.userState.allUsers;
export const selectAllUserproxys = (state) => state.userState.allUserproxys;
export const selectAllCustomers = (state) => state.userState.allCustomers;
export const selectCustomers = (state) => state.userState.customers;
export const selectGetUser = (state) => state.userState.get_user;
export const selectUserRole = (state) => state.userState.user && state.userState.user.role;
export const selectUpdatingUser = (state) => state.userState.updatingUser;
export const selectGettingUserData = (state) => state.userState.gettingUserData;
export const selectTotalUsers = (state) => state.userState.totalUsers;

export default userSlice.reducer;