import { createSlice } from '@reduxjs/toolkit';
import { userService } from '../services/users.service';
import { getUserData } from './userReducer';
import { openSnackBar } from './snackBarReducer';
import { setUser } from './userReducer';
import Cookie from "js-cookie";

let userToken = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : null;

//reducers
export const authSlice = createSlice({
    name: 'authentication',
    initialState: {
        loggingIn: false,
        loggedIn: userToken ? true : false,
        userToken: userToken,
        registering: false,
        successRegister: false,
    },
    reducers: {
        loginRequest: (state) => {
            state.loggingIn = true
            state.loggedIn = false;
        },
        loginSuccess: (state, action) => {
            state.loggingIn = false;
            state.loggedIn = true;
            state.userToken = action.payload;
            Cookie.set('token', action.payload);
        },
        loginFailure: (state) => {
            state.loggingIn = false;
            state.loggedIn = false;
        },
        registerRequest: (state) => {
            state.registering = true;
        },
        registerEnd: (state) => {
            state.registering = false;
        },
        registerSuccess: (state, action) => {
            state.successRegister = action.payload;
        },
        logout: (state) => {
            localStorage.removeItem('user');
            state.loggedIn = false;
            state.userToken = null;
            Cookie.remove('token');
        }
    }
});

//actions
export const { loginRequest, loginSuccess, loginFailure, registerRequest, registerEnd, registerSuccess, logout } = authSlice.actions;

export const logoutRequest = () => async (dispatch) => {
    try {
        await userService.logout();
        dispatch(logout());
        dispatch(setUser({}))
        dispatch(openSnackBar({ message: 'Successfully sign out', status: 'success' }))
    }  catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const logUserIn = (email, password, role, from) => async (dispatch) => {
    dispatch(loginRequest());

    try {
        const respData = await userService.login(email, password, role);
        dispatch(loginSuccess(respData));
        dispatch(setUser(respData.user));
        // if (respData.data.user.userproxy && respData.data.user.userproxy.length > 0) {
        //     const username = respData.data.user.userproxy[0].username;
        //     dispatch(openSnackBar({ message: `Hello ${username}!`, status: 'info' }));
        // } else {
        //     const username = respData.data.user.businessname;
        //     dispatch(openSnackBar({ message: `Hello ${username}!`, status: 'info' }));
        // }
        
        // window.location.replace(from ? from : '/')
    } catch (error) {
        dispatch(loginFailure())
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
    }
};

export const registerUser = (user) => async (dispatch) => {
    dispatch(registerRequest());
    if (user.role === 'merchant') {
        dispatch(loginRequest());
    }
    dispatch(registerSuccess(false));

    try {
        const respData = await userService.registerUser(user);                
        if (user.role === 'merchant') {         
            dispatch(loginSuccess(respData));
            dispatch(setUser(respData.data.user));
        }
        dispatch(registerSuccess(true));
        dispatch(openSnackBar({ message: `Correctly registered the user ${user.email} as ${user.role}`, status: 'success' }));
        dispatch(registerEnd());
    } catch (error) {
        dispatch(registerEnd());
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const resetUser = (email, from) => async (dispatch) => {
    try {
        await userService.reset(email);
        dispatch(openSnackBar({ message: `Sent reset email to the ${email}!`, status: 'info' }));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const resetUserPwd = (token, password, from) => async (dispatch) => {
    try {
        await userService.resetpwd(token, password);
        dispatch(openSnackBar({ message: `Successfully reset password!`, status: 'info' }));
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
};

export const verifyUser = (token, from) => async (dispatch) => {
    try {
        await userService.verifyUser(token);
        dispatch(getUserData(JSON.parse(localStorage.user).user.id));
        dispatch(openSnackBar({ message: `Successfully verified the user!`, status: 'info' }));
        
    } catch (error) {
        dispatch(openSnackBar({ message: error.message, status: 'error' }))
        throw new Error(error);
    }
}

//selectors
export const selectLogingIn = (state) => state.authState.loggingIn;
export const selectLoggedIn = (state) => state.authState.loggedIn;
export const selectUserToken = (state) => state.authState.userToken;
export const selectRegistering = (state) => state.authState.registering;
export const selectRegisterSuccess = (state) => state.authState.successRegister;

export default authSlice.reducer;